// context/AuthContext.js
import { createContext, useContext, useState } from "react";

const AuthContext = createContext();

export function AuthProvider({ children }) {
  // ŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿßŸÅÿ™ÿ±ÿßÿ∂Ÿä (ŸäŸÖŸÉŸÜ ÿ™ÿπÿØŸäŸÑŸá ŸÑÿßÿ≠ŸÇŸãÿß ŸÖŸÜ ÿ¥ÿßÿ¥ÿ© ÿßŸÑÿØÿÆŸàŸÑ)
  const [user, setUser] = useState({
    name: "ÿßŸÑŸÖÿØŸäÿ± ÿ£ÿ≠ŸÖÿØ",
    role: "admin", // admin | cashier | pharmacist
  });

  // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™
  const hasPermission = (roles) => roles.includes(user.role);

  return (
    <AuthContext.Provider value={{ user, setUser, hasPermission }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  return useContext(AuthContext);
}










