// context/InventoryContext.js
import { createContext, useContext, useState } from "react";

const InventoryContext = createContext();

export function InventoryProvider({ children }) {
  const [products, setProducts] = useState([
    {
      id: 1,
      name: "ÿ®ÿßÿ±ÿßÿ≥Ÿäÿ™ÿßŸÖŸàŸÑ 500mg",
      sku: "PRC500",
      company: "ÿµŸäÿØŸÑŸäÿßÿ™ ÿßŸÑŸÖÿ™ÿ≠ÿØÿ©",
      category: "ŸÖÿ≥ŸÉŸÜÿßÿ™",
      purchasePrice: 8,     // ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°
      price: 12,            // ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ
      quantity: 30,
      minQty: 10,
      expiryDate: "2025-04-10",
      stockHistory: [],
    },
    {
      id: 2,
      name: "ŸÅŸäÿ™ÿßŸÖŸäŸÜ ÿ≥Ÿä 1000mg",
      sku: "VTC1000",
      company: "ÿßŸÑÿµÿ≠ÿ© ÿßŸÑÿπÿßŸÑŸÖŸäÿ©",
      category: "ŸÅŸäÿ™ÿßŸÖŸäŸÜÿßÿ™",
      purchasePrice: 12,
      price: 18,
      quantity: 10,
      minQty: 5,
      expiryDate: "2024-12-15",
      stockHistory: [],
    },
    {
      id: 3,
      name: "ŸÖÿ∂ÿßÿØ ÿ≠ÿ≥ÿßÿ≥Ÿäÿ©",
      sku: "ANTHST",
      company: "ŸáŸäŸàŸÖŸÜ ŸÅÿßÿ±ŸÖÿß",
      category: "ÿ≠ÿ≥ÿßÿ≥Ÿäÿ©",
      purchasePrice: 18,
      price: 25,
      quantity: 5,
      minQty: 5,
      expiryDate: "2024-11-01",
      stockHistory: [],
    },
  ]);

  const getProduct = (id) =>
    products.find((p) => p.id === Number(id));

  const updateStock = (id, qty, type = "in") => {
    setProducts((prev) =>
      prev.map((p) => {
        if (p.id !== id) return p;
        const newQty = type === "in" ? p.quantity + qty : p.quantity - qty;
        return {
          ...p,
          quantity: newQty < 0 ? 0 : newQty,
          stockHistory: [
            ...p.stockHistory,
            {
              type,
              qty,
              date: new Date().toLocaleString(),
            },
          ],
        };
      })
    );
  };

  const decreaseStockOnSale = (id, qty) => updateStock(id, qty, "out");
  const increaseStockOnReturn = (id, qty) => updateStock(id, qty, "in");

  const getWarnings = (p) => {
    const warnings = [];
    if (!p) return warnings;

    if (p.expiryDate) {
      const daysLeft =
        (new Date(p.expiryDate) - new Date()) /
        (1000 * 60 * 60 * 24);

      if (daysLeft < 0) warnings.push("‚ùå ÿßŸÑŸÖŸÜÿ™ÿ¨ ŸÖŸÜÿ™ŸáŸä ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©!");
      else if (daysLeft < 30)
        warnings.push(
          `‚ö†Ô∏è ÿßŸÑŸÖŸÜÿ™ÿ¨ ÿ¥ÿßÿ±ŸÅ ÿπŸÑŸâ ÿßŸÑÿßŸÜÿ™Ÿáÿßÿ° ÿÆŸÑÿßŸÑ ${Math.ceil(daysLeft)} ŸäŸàŸÖ`
        );
    }

    if (p.quantity < (p.minQty || 5)) {
      warnings.push("üî¥ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ ÿ£ŸÇŸÑ ŸÖŸÜ ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ÿØŸÜŸâ");
    }

    return warnings;
  };

  const printInventoryReport = () => {
    const w = window.open("", "", "width=900,height=700");

    w.document.write(`
      <html dir="rtl" lang="ar">
      <head>
        <title>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ</title>
        <style>
          body { font-family: 'Tajawal', sans-serif; padding:20px; }
          h2 { text-align:center; margin-bottom:10px; }
          table { width:100%; border-collapse:collapse; font-size:13px; }
          th, td { border:1px solid #ddd; padding:6px; text-align:right; }
          th { background:#f3f4f6; }
        </style>
      </head>
      <body>
        <h2>üì¶ ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ</h2>
        <table>
          <thead>
            <tr>
              <th>ÿßŸÑŸÖŸÜÿ™ÿ¨</th>
              <th>ÿßŸÑŸÉŸàÿØ</th>
              <th>ÿßŸÑŸÅÿ¶ÿ©</th>
              <th>ÿßŸÑÿ¥ÿ±ŸÉÿ©</th>
              <th>ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°</th>
              <th>ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ</th>
              <th>ÿßŸÑŸÉŸÖŸäÿ©</th>
              <th>ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ÿØŸÜŸâ</th>
              <th>ÿ™ÿßÿ±ŸäÿÆ ÿßŸÑÿßŸÜÿ™Ÿáÿßÿ°</th>
            </tr>
          </thead>
          <tbody>
            ${products
              .map(
                (p) => `
              <tr>
                <td>${p.name}</td>
                <td>${p.sku}</td>
                <td>${p.category}</td>
                <td>${p.company}</td>
                <td>${p.purchasePrice || ""}</td>
                <td>${p.price}</td>
                <td>${p.quantity}</td>
                <td>${p.minQty}</td>
                <td>${p.expiryDate || ""}</td>
              </tr>
            `
              )
              .join("")}
          </tbody>
        </table>
      </body>
      </html>
    `);

    w.document.close();
    w.print();
  };

  const updateProduct = (id, updates) => {
    setProducts((prev) =>
      prev.map((p) => (p.id === Number(id) ? { ...p, ...updates } : p))
    );
  };

  return (
    <InventoryContext.Provider
      value={{
        products,
        setProducts,
        getProduct,
        getWarnings,
        updateStock,
        decreaseStockOnSale,
        increaseStockOnReturn,
        printInventoryReport,
        updateProduct,
      }}
    >
      {children}
    </InventoryContext.Provider>
  );
}

export function useInventory() {
  return useContext(InventoryContext);
}













